import storeList from '../../component/store-list/store-list.vue';

export default {
	data() {
		return {
			swipers: [],
			goods: [],
			navs: [
				{
					icon: 'iconfont .icon-chaoshishangpin',
					title: '乐同商城',
					path: '/pages/nav/store/store'
				},
				{
					icon: 'iconfont .icon-kebiao',
					title: '课程介绍',
					path: '/pages/nav/courseB/courseB'
				},
				{
					icon: 'iconfont .icon-shipin',
					title: '学习视频',
					path: '/pages/nav/videos/videos'
				},
				{
					icon: 'iconfont .icon-lianxi',
					title: '联系我们',
					path: '/pages/nav/contact/contact'
				}
			]
		};
	},
	// 页面加载前调用
	onLoad() {
		this.getSwipers();
		this.getHotGoogs();
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			// 来自页面内分享按钮
			console.log(res.target);
		}
		return {
			title: '分享标题',
			path: '/pages/index/index'
		};
	},
	components: {
		'store-list': storeList
	},
	methods: {
		// 获取轮播图数据
		async getSwipers() {
			const res = await this.$myRequest({
				url: '/carousel/carousel'
			});
			console.log(res.data.data);
			this.swipers = res.data.data.records;
		},
		// 获取热门商品数据
		async getHotGoogs() {
			const res = await this.$myRequest({
				url: '/product/product?num=1'
			});
			console.log(res.data.data.records);
			this.goods = res.data.data.records;
		},
		// 导航点击的处理函数
		navItemClick(url) {
			// console.log(url);
			uni.navigateTo({
				url: url
			});
		},
		// 跳转到商品详情页
		goGoodsDetail(id) {
			uni.navigateTo({
				url: '../goods-detail/goods-detail?id=' + id
			});
		}
		// onShareAppMessage(e) {
		// 	let title = '转发成功';
		// 	return {
		// 		title: title,
		// 		path: '/pages/index/index'
		// 	};
		// }
	}
};
